import { Table } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import { addCount } from "./../store.js";

function Cart() {
  let list = useSelector((state) => state);
  let dispatch = useDispatch();
  // dispatch는 store.js에 요청을 보내주는 함수

  return (
    <div>
      <h5>{list.user.name} 님의 장바구니</h5>
      <Table striped bordered hover>
        <thead>
          <tr>
            <th>#</th>
            <th>Name</th>
            <th>Count</th>
            <th>수량변경</th>
            <th>Price</th>
          </tr>
        </thead>
        <tbody>
          {list.cart.map((a, i) => (
            <tr key={i}>
              <td>{list.cart[i].id}</td>
              <td>{list.cart[i].name}</td>
              <td>{list.cart[i].count}</td>
              <td>
                <button
                  onClick={() => {
                    // dispatch(addCount(i));
                    dispatch(addCount(list.cart[i].id));
                  }}
                >
                  +
                </button>
              </td>
              <td>{list.cart[i].price}</td>
              <td></td>
            </tr>
          ))}
        </tbody>
      </Table>
    </div>
  );
}

export default Cart;
